import { NATION } from "./Const";
import { Equitment } from "./Equipment";

export class FlightDeck extends Equitment{
    public nation:NATION;
    public scout:number;
    public fighter:number;
    public torpedo:number;
    public bomber:number;
    public squadron:number;
    public init(row:any){
        this.id = row['Serial_SNDB'];
        this.nation = row['NationCode'];
        this.scout = row['Scout'];
        this.fighter = row['Fighter'];
        this.torpedo = row['Torpedo'];
       
        this.bomber = row['Dive'];
        this.squadron = row['Squadron'];
    }

    public export(){
        let base_attr = super.export();
        let obj_attr = [
            this.scout+'',
            this.fighter.toString(),
            this.torpedo.toString(),
            this.bomber.toString(),
            this.squadron.toString(),
        ];
        return obj_attr.concat(base_attr);
    }
}