import { NATION } from "./Const";
import { Equitment } from "./Equipment";

export class FCS extends Equitment{
    public name:string;
    public nation:NATION;
    public acc:number;
    public torpedodist:number;
    public sightdist:number;
    public radarrange:number;
    public sonarrange:number;
    public criticalrate:number;

    public init(row:any){
        this.id = row['Serial_SNDB'];
        this.name = row['FCSName'];
        this.nation = row['NationCode'];
        this.acc = row['AccuracyBonus'];
        this.torpedodist = row['TorpedoDistance'];
        this.sightdist = row['ViewDistance'];
        this.radarrange = row['RadarRange'];
        this.sonarrange = row['SonarRange'];
        this.criticalrate = row['CriticalBonus'];
    }

    public export(){
        let base_attr = super.export();
        let obj_attr = [
            this.name,
            "+"+this.acc,
            this.sightdist.toString(),
            this.radarrange.toString(),
            this.sonarrange.toString(),
            this.torpedodist.toString(),
            `+${this.criticalrate}%`,
        ];

        return obj_attr.concat(base_attr);
    }
}