import { NATION, LAUNCHER_SHELLTYPE, time2sec } from "./Const";
import { Equitment } from "./Equipment";

export class Launcher extends Equitment{
    public name:string;
    public nation:NATION;
    public bundle:number;
    public turnspeed:number;
    public anglenarrow:number;
    public anglewide:number;
    public reloadtime:number;
    public shelltype:LAUNCHER_SHELLTYPE;
    public speedlo:number;
    public speedhi:number;
    public hittype:number;//0=1=/
    public minrange:number;//?
    public speedlorange:number;
    public speedhirange:number;
    public hedamage:number;


    public init(row:any){
        this.id = row['Serial_SNDB'];
        this.name = row['TpLauncherName'];
        this.nation = row['NationCode'];
        this.bundle = row['Bundle'];
        this.turnspeed = row['Task_Speed'];
        this.anglenarrow = row['FireAngleNarrow'];
        this.anglewide = row['FireAngleWide'];
        this.reloadtime = row['ReloadTime'];
        this.shelltype = row['ShellType'];
        this.speedlo = row['SpeedLo'];
        this.speedhi = row['SpeedHi'];
        this.hittype = row["HitType"];
        this.minrange = row['MinRange'];
        this.speedlorange = row['Crusing_Range_SpeedLo'];
        this.speedhirange = row['Crusing_Range_SpeedHi'];
        this.hedamage = row['HEDamage'];

    }

    public export(){
        let base_attr = super.export();

        let speed_str = '';
        if (this.shelltype == LAUNCHER_SHELLTYPE.TORPEDO){
            speed_str = this.speedlo + ' Kn';
        }else{
            speed_str = "";
        }

        let obj_attr = [
            this.name,
            'x'+this.klass.count,
            'x'+this.bundle.toString(),
            this.hedamage.toString(),
            this.speedlorange.toString(),
            `${time2sec(this.reloadtime)}`,
            speed_str
        ];        
        return obj_attr.concat(base_attr);
    }
}