import { AIRCRAFT_TYPE } from "./Const";
import { Equitment } from "./Equipment";
import { Translation } from "./Translation";

let plain_type_map = [
    {v:AIRCRAFT_TYPE.SCOUT,s:"LS_SCOUTS"},
    {v:AIRCRAFT_TYPE.FIGHTER,s:"LS_FIGHTERS"},
    {v:AIRCRAFT_TYPE.TORPEDO,s:"LS_TORPEDO_BOMBERS"},
    {v:AIRCRAFT_TYPE.BOMBER,s:"LS_DIVE_BOMBERS"},
]

export class Plain{
    public id:number;
    public name:string;
    public type:AIRCRAFT_TYPE;
    public sightrange:number;
    public DP:number;
    public speed:number;
    public fuel:number;
    public attack:number;
    public attackgap:number;
    public defenceap:number;
    public readytime:number;
    public torpedospeed:number;
    public torpedodamage:number;
    public hedamage:number;
    public apdamagebouns:number;

    public init(row:any){
        this.id = row['Serial_SNDB'];
        this.name = row['AircraftName'];
        this.type = row['AirCraftType'];
        this.sightrange = row['SightRange'];
        this.DP = row['DP'];
        this.speed = row['Speed'];
        this.fuel = row['Fuel'];
        this.attack = row['Attack'];
        this.attackgap = row['AttackGap'];
        this.defenceap = row['DefenceAP'];
        this.readytime = row['ReadyTime'];
        this.torpedospeed = row['SpeedHi'];
        this.torpedodamage = row['TorpedoHEDamage'];
        this.hedamage = row['HEDamage'];
        this.apdamagebouns = row['APDamageBonus'];
    }

    public export(){
        let that = this;
        let type_str = plain_type_map.find((p)=>{
            return p.v == that.type;
        }).s;
        let loc_str = Translation.str(type_str, type_str);

        let obj_attr = [
            this.name,
            loc_str,
            this.sightrange,
            this.DP,
            this.speed,
            this.fuel,
            this.attack,
            this.attackgap,
            this.defenceap,
            this.readytime,
            this.torpedospeed,
            this.torpedodamage,
            this.hedamage,
            this.apdamagebouns,

        ];

        return obj_attr;
    }
}

export class AirCraft extends Equitment{
    public plainMap:Map<AIRCRAFT_TYPE,Plain>;
    public constructor(){
        super();
        this.plainMap = new Map<AIRCRAFT_TYPE, Plain>();
    }

    public exportAircraftPlains(){
        let base_attr = super.export();
        let obj_attr = [];
        for (let plain of this.plainMap.values()){
            obj_attr.push(plain.export().concat(base_attr));
        }

        return obj_attr;
    }
}