import { AsyncHelper } from "./AsyncHelper";

export class Translation{
    private static m_trans:Map<string,string>;

    public static async init(lang:string){
        this.m_trans = new Map<string,string>();
        let lang_file_path = `./asset/localizable/${lang}/Localizable.strings`;
        let content = await AsyncHelper.GetAsync(lang_file_path);
        content = content.replace('\n','');
        let lines = content.split(';');

        for (let line of lines){
            if (line == ''){
                continue;
            }
            
            let kv = line.split('=');
            if (kv[0]=='' || kv[1] == undefined){
                continue;
            }
            
            kv[0] = this.Trim(kv[0]);
            kv[1] = this.Trim(kv[1]);
            kv[0] = kv[0].substr(1,kv[0].length-2);
            kv[1] = kv[1].substr(1,kv[1].length-2);
            //console.log(`k=${kv[0]}v=${kv[1]}`);

            this.m_trans.set(kv[0], kv[1]);
        }
        console.log(this.m_trans.size+" loaded");
    }

    public static str(s:string, def_str:string){
        if (this.m_trans.has(s)){
            return this.m_trans.get(s);
        }
        return def_str;
    }
    public static Trim(str:string){ 
        return str.replace(/(^\s*)|(\s*$)/g, ""); 
    }
}