import { NATION, time2sec, speed2kn } from "./Const";
import { Equitment } from "./Equipment";

export class Engine extends Equitment{
    public name:string;
    public nation:NATION;
    public maxspeed:number;
    public overspeed:number;
    public overtime:number;

    public init(row:any){
        this.id = row['Serial_SNDB'];
        this.name = row['EngineName'];
        this.nation = row['NationCOde'];
        this.maxspeed = row['ShipMaxSpeed'];
        this.overspeed = row['ShipOverSpeed'];
        this.overtime = row['ShipOverSpeedTime'];
    }

    public export(){
        let base_attr = super.export();
        let obj_attr = [
            this.name,
            `${speed2kn(this.maxspeed)}`,
            `${speed2kn(this.overspeed)}`,
            `${this.overtime} Sec`,
        ];
        return obj_attr.concat(base_attr);
    }
}