import { Ship } from "./ship";
import { DbHelper } from "./dbhelper";
import { NATION, SHIPCLASS, SHIPGRADE, EQUIPMENT_TYPE } from "./Const";
import { Translation } from "./Translation";

(<any>window).AsyncStart = function(){
    Main.Start();
}



export class Main{
    private static m_jq_cb_dbver:any;
    private static m_jq_cb_nation:any;
    private static m_jq_cb_shipclass:any;
    private static m_jq_cb_shipgrade:any;
    private static m_jq_cb_shipname:any;
    // ship
    private static m_jq_img_shipimage:any;
    private static m_jq_base_div:any;
    // tabs
    private static m_jq_tab_base_table:any;
    private static m_jq_tab_extra_table:any;
    private static m_jq_tab_gun_table:any;
    private static m_jq_tab_autogun_table:any;
    private static m_jq_tab_launcher_table:any;
    private static m_jq_tab_aircraft_table:any;
    private static m_jq_tab_fcs_table:any;
    private static m_jq_tab_engine_table:any;
    private static m_jq_tab_flightdeck_table:any;
    private static m_jq_tab_armor_table:any;
    private static m_jq_tab_oxygen_table:any;
    private static m_jq_tab_crew_table:any;
    private static m_jq_win_loading:any;

    //local 
    private static m_inited:any;

    private static m_nation:NATION;
    private static m_shipclass:SHIPCLASS;
    private static m_shipgrade:SHIPGRADE;
    private static m_last_shiplist:any;
    private static m_selected_ship:any;

    public static async Start(){
        this.init();
        await DbHelper.init();
        this.m_jq_win_loading.window('open');
        await Translation.init('en');
        console.log("nfm start");
        this.m_inited = true;
        this.m_jq_win_loading.window('close');

        //this.exportKeyWords();
    }

    private static init(){
        
        let that = this;
        this.m_inited = false;

        this.m_jq_cb_dbver = $('#dbversion');
        this.m_jq_cb_dbver.combobox({
            editable:false,
        });
        this.m_jq_cb_nation = $('#nation');
        this.m_jq_cb_nation.combobox({
            valueField:"value",
            textField:"text",
            editable:false,
            onSelect: function(rec:any){
                //console.log(rec);
                that.m_nation = rec.value;
                that.refreshL1();
            }
        });
        this.m_jq_cb_nation.combobox("setValue",NATION.ALL);

        this.m_jq_cb_shipclass = $('#shipclass');
        this.m_jq_cb_shipclass.combobox({
            valueField:"value",
            textField:"text",
            editable:false,
            onSelect: function(rec:any){
                that.m_shipclass = rec.value;
                that.refreshL1();
            }
        });
        this.m_jq_cb_shipclass.combobox("setValue",SHIPCLASS.ALL);

        this.m_jq_cb_shipgrade = $('#shipgrade');
        this.m_jq_cb_shipgrade.combobox({
            valueField:"value",
            textField:"text",
            editable:false,
            onSelect: function(rec:any){
                that.m_shipgrade = rec.value;
                that.refreshL1();
            }
        });
        this.m_jq_cb_shipgrade.combobox("setValue",SHIPGRADE.ALL);

        this.m_jq_cb_shipname = $('#shipname');
        this.m_jq_cb_shipname.combobox({
            valueField:"value",
            textField:"text",
            editable:false,
            onSelect: function(rec:any){
                let idx = that.m_last_shiplist.findIndex((s:any)=>{
                    return s.Serial_SNDB == rec.value;
                });
                if (idx != -1){
                    that.m_selected_ship = that.m_last_shiplist[idx];
                }else{
                    that.m_selected_ship = null;
                }
                that.refreshL2();
                
            }
        }); 

        
        this.m_jq_base_div = $('base_div');
        this.m_jq_img_shipimage = $('#ship_img');
        this.m_jq_tab_base_table = $('#tab_base_table');
        this.m_jq_tab_extra_table = $('#tab_extra_table');
        this.m_jq_tab_gun_table = $('#tab_gun_table');
        this.m_jq_tab_autogun_table = $('#tab_autogun_table');
        this.m_jq_tab_launcher_table = $('#tab_launcher_table');
        this.m_jq_tab_aircraft_table = $('#tab_aircraft_table');
        this.m_jq_tab_fcs_table = $('#tab_fcs_table');
        this.m_jq_tab_engine_table = $('#tab_engine_table');
        this.m_jq_tab_flightdeck_table = $('#tab_flightdeck_table');
        this.m_jq_tab_armor_table = $('#tab_armor_table');
        this.m_jq_tab_oxygen_table = $('#tab_oxygen_table');
        this.m_jq_tab_crew_table = $('#tab_crew_table');
        this.m_jq_win_loading = $('#loading');
        
    }


    private static refreshL1(){
        if (!this.m_inited){
            return;
        }
        let param = Object();
        if (this.m_nation != NATION.ALL){
            param.Nation = this.m_nation;
        }
        if (this.m_shipclass != SHIPCLASS.ALL){
            param.Class = this.m_shipclass;
        }
        if (this.m_shipgrade != SHIPGRADE.ALL){
            param.Grade = this.m_shipgrade;
        }

        this.m_last_shiplist = DbHelper.query("SHIP", param);
        //console.log(this.m_last_shiplist);
        let data = [];
        for (let row_ship of this.m_last_shiplist){
            let in_shop = row_ship['Shop'];
            if (in_shop == 1 || in_shop == 2){
                let full_name = this.getShipFullName(row_ship);
                data.push({
                    value:row_ship.Serial_SNDB,
                    text:full_name,
                })
            }

        }
        this.m_jq_cb_shipname.combobox("loadData", data);
    }

    private static getShipClassStr(c:SHIPCLASS){
        if (c == SHIPCLASS.DD){
            return 'DD';
        }else if (c == SHIPCLASS.CL){
            return 'CL';
        }else if (c == SHIPCLASS.CA){
            return 'CA';
        }else if (c == SHIPCLASS.BB){
            return 'BB';
        }else if (c == SHIPCLASS.CV){
            return 'CV';
        }else if (c == SHIPCLASS.SS){
            return 'SS';
        }else{
            return c.toString();
        }
    }
    private static getShipGradeStr(c:SHIPGRADE){
        if (c == SHIPGRADE.S){
            return 'S';
        }else if (c == SHIPGRADE.A){
            return 'A';
        }else if (c == SHIPGRADE.B){
            return 'B';
        }else if (c == SHIPGRADE.C){
            return 'C';
        }else if (c == SHIPGRADE.D){
            return 'D';
        }else if (c == SHIPGRADE.E){
            return 'E';
        }else if (c == SHIPGRADE.F){
            return 'F';
        }else{
            return '?';
        }
    }
    private static getShipFullName(row:any){
        let ship_name = row.FrameName.substr(3);
        ship_name = Translation.str(row.FrameName, ship_name);
        let klass = this.getShipClassStr(row.Class);
        let grade = this.getShipGradeStr(row.Grade);
        return `[${klass}]${ship_name} - ${grade}`;
    }

    private static refreshL2(){
        if (this.m_selected_ship == null){
            this.m_jq_img_shipimage.src = "";
        }else{
            let asset_ships = DbHelper.query("ASSET_SHIP", {Serial_SNDB:this.m_selected_ship.Serial_SNDB});
            if (asset_ships != null && asset_ships.length > 0){
                let asset_ship = asset_ships[0];

                let ship_img_url = `./asset/data_ship/${asset_ship.UIFolder}/Image.png`;
                this.m_jq_img_shipimage.attr('src',ship_img_url);
                //this.m_jq_base_div.css('background-image',`url(${ship_img_url})`);
                //base
                let ship = new Ship();
                ship.init(this.m_selected_ship);

                this.makeTable(this.m_jq_tab_base_table, ship.exportBaseData());
                this.makeTable(this.m_jq_tab_extra_table, ship.exportExtraData());
                // gun
                this.makeLevelTable(this.m_jq_tab_gun_table, ship.exportGunsData(EQUIPMENT_TYPE.GUN));
                // autogun
                this.makeLevelTable(this.m_jq_tab_autogun_table, ship.exportGunsData(EQUIPMENT_TYPE.AUTO_GUN));
                // launcher
                this.makeLevelTable(this.m_jq_tab_launcher_table, ship.exportLaunchersData());
                // launcher
                this.makeLevelTable(this.m_jq_tab_engine_table, ship.exportEnginesData());
                // flightdeck
                this.makeLevelTable(this.m_jq_tab_flightdeck_table, ship.exportFlightDecksData());
                // aircraft
                this.makeLevelTable(this.m_jq_tab_aircraft_table, ship.exportAircraftData());


                // fcs
                this.makeLevelTable(this.m_jq_tab_fcs_table, ship.exportFCSsData());
                // armor
                this.makeLevelTable(this.m_jq_tab_armor_table, ship.exportArmorData());
                // oxygen
                this.makeLevelTable(this.m_jq_tab_oxygen_table, ship.exportOxygensData());
                // crew
                this.makeLevelTable(this.m_jq_tab_crew_table, ship.exportCrewsData());
            }
        }
    }


    // private static async GetAsync(path:string): Promise<any>{
    //     let loadedData = null;
    //     $.get(path, function(data) {
    //         loadedData = data;
    //     });
    //     while(loadedData == null){
    //         await this.wait();
    //     }
    //     return loadedData;
    // }
    private static wait(): Promise<void>{
        return new Promise<void>((resolve, reject)=>{setTimeout(()=>resolve(), 10)});
    }

    private static makeTable(jq_table:any, param:any){
        jq_table.children().remove();
        let jq_str = '';
        for (let i=0; i<param.titles.length; i++){
            
            jq_str+= `<tr><th>${param.titles[i].text}:</th><td>${param.data[i]}</td></tr>`;
        }
        jq_table.append(jq_str);
    }

    private static makeLevelTable(jq_table:any, param_list:any){
        jq_table.children().remove();
        let jq_str = '';
        if (param_list.titles != undefined){
            if (param_list.titles instanceof Array){
                // draw head
                jq_str+='<tr>';
                for (let title of param_list.titles){
                    jq_str+=`<th width="${title.width}px">${title.text}</th>`;
                }
                jq_str+='</tr>';
            }else{
                jq_str += param_list.titles;

            }

        }

/*
        {
            titles:[{text:string, width:number}],
            levels:[items:[{...}]}]
        }

*/
        // draw content
        for (let i=0;i<param_list.levels.length;i++){
            let level = param_list.levels[i];
            
            for (let j=0;j<level.items.length;j++){
                let item = level.items[j];
                jq_str+='<tr>';
                if (j==0){
                    jq_str+=`<td rowspan="${level.items.length}">Lv.${i}</td>`
                }
                for (let v of item){
                    jq_str+= `<td>${v}</td>`;
                }
                jq_str+='</tr>';
            }
            
        }

        jq_table.append(jq_str);
    }

    private static async exportKeyWords(){
        let keywords = [];
        let ls_names = [];
        let rs = DbHelper.query("SHIP", undefined);
        for (let row of rs){
            ls_names.push(row['FrameName']);
        }
        await Translation.init('zh-Hans');
        for (let ls of ls_names){
            keywords.push(Translation.str(ls, ls));
        }
        await Translation.init('en');
        for (let ls of ls_names){
            keywords.push(Translation.str(ls, ls));
        }
        console.log(keywords.join(","));

    }
}