export class AsyncHelper{
    public static wait(): Promise<void>{
        return new Promise<void>((resolve, reject)=>{setTimeout(()=>resolve(), 10)});
    }
    
    public static async GetAsync(path:string): Promise<any>{
        let loadedData = null;
        $.get(path, function(data) {
            loadedData = data;
        });
        while(loadedData == null){
            await this.wait();
        }
        return loadedData;
    }
}
