import { EQUIPMENT_TYPE } from "./Const";
import { AsyncHelper } from "./AsyncHelper";

export class DbHelper{
    private static m_db:any;
    public static async init(){
        this.m_db = null;
        let win = window as any;
        let SQL = win.SQL;
        let url_to_db = "./db/nfm_dbase.sql3";
        var xhr = new XMLHttpRequest();
        xhr.open('GET', url_to_db, true);
        xhr.responseType = 'arraybuffer';
        xhr.onload = function(e) {
            var loaded_data = new Uint8Array(this.response);
            DbHelper.m_db = new SQL.Database(loaded_data);
            // let rs = sql.exec('select * from SHIP');
            //console.log("");
        };
        xhr.send();

        while (this.m_db == null){
            await AsyncHelper.wait();
        }
    }



    public static query(dbname:string, filter:any){
        let sql_str = `select * from ${dbname}`;
        if (filter){
            sql_str += ` where `;
            let condition_str = [];
            for (let key in filter){
                condition_str.push(`${key}=${filter[key]}`);
            }
            sql_str+= condition_str.join(' and ');
        }
        console.log(sql_str);
        let sql_rs = this.m_db.exec(sql_str);
        let rs:any[] = [];
        if (sql_rs.length == 0){
            return rs;
        }
        for (let i=0;i<sql_rs[0].values.length;i++){
            let row = sql_rs[0].values[i];
            let json_row = Object();
            // make a json style row
            for (let col_idx =0 ;col_idx< sql_rs[0].columns.length; col_idx++){
                let col = sql_rs[0].columns[col_idx];
                json_row[col] = row[col_idx];
                //console.log(`write key${col.name}=>${row[col_idx]}`);
            }
            rs.push(json_row);
        }
        return rs;
    }
    
    public EquipmentType2Enum(type:string){
        switch (type){
            case 'Gun':return EQUIPMENT_TYPE.GUN;
            case 'AutoGun':return EQUIPMENT_TYPE.AUTO_GUN;
            case 'FCS':return EQUIPMENT_TYPE.FCS;
            case 'Engine':return EQUIPMENT_TYPE.ENGINE;
            case 'Launcher':return EQUIPMENT_TYPE.LAUNCHER;
            case 'Armor':return EQUIPMENT_TYPE.ARMOR;
            case 'Hull':return EQUIPMENT_TYPE.HULL;
            case 'Aircraft':return EQUIPMENT_TYPE.AIRCRAFT;
            case 'FlightDeck':return EQUIPMENT_TYPE.FLIGHTDECK;
            case 'Oxygen':return EQUIPMENT_TYPE.OXYGEN;
            default:return -1;
        }
    }
}