import { SHIPGRADE, SHIPCLASS, CREW_TYPE } from "./Const";
import { Translation } from "./Translation";

let abiliti_rate = [
    0.5,
    1,
    1.5,
    2,
    2.5
];

let type_map = [
    {v:CREW_TYPE.OPERATOR, s:'LS_SAILOR_OPERATOR'},
    {v:CREW_TYPE.ARMAMENT, s:'LS_SAILOR_ARMAMENT'},
    {v:CREW_TYPE.ENGINE, s:'LS_SAILOR_ENGINE'},
    {v:CREW_TYPE.REPAIR, s:'LS_SAILOR_REPAIR'},
    {v:CREW_TYPE.SEAMAN, s:'LS_SAILOR_SEAMAN'},
    {v:CREW_TYPE.RADIO, s:'LS_SAILOR_RADIO'},
    {v:CREW_TYPE.AVIATION, s:'LS_SAILOR_AVIATION'},
]

export class Crew{
    public klass:SHIPCLASS;
    public type:CREW_TYPE;

    public health:number;
    public aaw:number;
    public turn:number;
    public divetime:number;
    public accurancy:number;
    public sightrange:number;
    public gunreload:number;
    public torpedoreload:number;
    public maxspeed:number;
    public overheadspeed:number;
    public overheadtime:number;
    public repair:number;
    public reducedamage:number;

    public airlaunchtime:number;
    public airspeed:number;
    public airfuel:number;
    public airsightrange:number;
    public upgrade_xp:number[];
    public upgrade_time:number[];
    public upgrade_cost_fuel:number[];

    public master_cost_fuel:number;
    public master_cost_credit:number;
    public master_cost_steel:number;

    



    public init(row:any){
        
        this.klass=row['CLASS'];
        this.type=row['TEAM'];
        this.health=row['HEALTH'];
        this.aaw=row['AAW'];
        this.turn=row['TURN'];
        this.divetime=row['DIVE_TIME'];
        this.accurancy=row['ACCURANCY'];
        this.sightrange=row['SIGHT_RANGE'];
        this.gunreload=row['GUN_RELOAD'];
        this.torpedoreload=row['TORPEDO_RELOAD'];
        this.maxspeed=row['SPEED'];
        this.overheadspeed=row['OVERHEAT'];
        this.overheadtime=row['OVERHEAT_TIME'];
        this.repair=row['REPAIR'];
        this.reducedamage=row['REDUCE_DAMAGE'];
        this.airlaunchtime=row['AIR_LAUNCH_TIME'];
        this.airspeed=row['AIR_SPEED'];
        this.airfuel=row['AIR_FUEL'];
        this.airsightrange=row['AIR_SIGHT_RANGE'];

        this.upgrade_xp = [];
        this.upgrade_time = [];
        this.upgrade_cost_fuel = [];
        for (let i=0;i<5;i++){
            this.upgrade_xp[i]=0;
            this.upgrade_time[i]=0;
            this.upgrade_cost_fuel[i]=0;
        }

        for (let i=1;i<4;i++){
            this.upgrade_xp[i]=row[`XP${i}`];
            this.upgrade_time[i]=row[`TIME${i}`];
            this.upgrade_cost_fuel[i]=row[`TrainCost${i}`];
        }

        this.master_cost_credit = row['MasterCost'];
        this.master_cost_fuel = row['MasterCostFuel'];
        this.master_cost_steel = row['MasterCostSteel'];

    }

    public export(level:number){
        let type_s = type_map.find(t=>t.v==this.type).s;
        let type_str = Translation.str(type_s, type_s); 

        let upgrade_xp = '';
        let upgrade_time = '';
        let upgrade_cost_fuel = '';

        let master_credit = '';
        let master_steel = '';
        let master_fuel = '';

        if (level == 0 || level == 4){
            if (level == 4){
                master_credit = this.master_cost_credit.toString();
                master_steel = this.master_cost_steel.toString();
                master_fuel = this.master_cost_fuel.toString();
            }
        }else{
            upgrade_xp = this.upgrade_xp[level].toString();
            upgrade_time = this.upgrade_time[level].toString();
            upgrade_cost_fuel = this.upgrade_cost_fuel[level].toString();
        }


        let abrate = abiliti_rate[level];
        let obj_attr = [
            type_str,
            Math.floor(this.health * abrate),
            Math.floor(this.aaw * abrate),
            Math.floor(this.turn * abrate),
            Math.floor(this.divetime * abrate),
            Math.floor(this.accurancy * abrate),
            Math.floor(this.sightrange * abrate),
            Math.floor(this.gunreload * abrate),
            Math.floor(this.torpedoreload * abrate),
            Math.floor(this.maxspeed * abrate),
            Math.floor(this.overheadspeed * abrate),
            Math.floor(this.overheadtime * abrate),
            Math.floor(this.repair * abrate),
            Math.floor(this.reducedamage * abrate),
            Math.floor(this.airlaunchtime * abrate),
            Math.floor(this.airspeed * abrate),
            Math.floor(this.airfuel * abrate),
            Math.floor(this.airsightrange * abrate),
            upgrade_xp,
            upgrade_time,
            upgrade_cost_fuel,
            this.master_cost_credit,
            this.master_cost_fuel,
            this.master_cost_steel,
        ];



        return obj_attr;
    }
}