import { Equitment } from "./Equipment";

export class Oxygen extends Equitment{
    public divetime:number;
    public init(row:any){
        this.id = row['Serial_SNDB'];
        this.divetime = row['DiveTime'];
    }

    public export(){
        let base_attr = super.export();
        let obj_attr = [
            this.divetime.toString(),
        ]
        return obj_attr.concat(base_attr);
    }
}