import { NATION, time2sec } from "./Const";
import { Equitment } from "./Equipment";

export class Gun extends Equitment{
    public name:string;
    public nation:NATION;
    public barrelnum:number;
    public maxanglev:number;
    public reloadtime:number;
    public turnspeed:number;
    public hedamage:number;
    public calibre:number;
    public aaw:number;
    public aadamage:number;
    public range:number;

    public init(gunrow:any){
        this.id = gunrow['Serial_SNDB'];
        this.name = gunrow['GunSetName'];
        this.nation = gunrow['NationCode'];
        this.barrelnum = gunrow['TurretType'];
        this.maxanglev = gunrow['AngleMaxVertical'];
        this.reloadtime = gunrow['ReloadTime'];
        this.turnspeed = gunrow['TaskSpeed'];
        this.hedamage = gunrow['HEDamage'];
        this.calibre = gunrow['Calibre'];
        this.aaw = gunrow['AAW'];
        this.aadamage = gunrow['AADamage'];
        this.range = gunrow['GameMaxDist'];
    }
    public export(){


        let base_attr = super.export();
        let attr = [
            this.name,
            'x'+this.klass.count,
            "x"+this.barrelnum,
            (this.calibre/100).toFixed(2) + '"',
            this.hedamage.toString(),
            this.range.toString(),
            time2sec(this.reloadtime),
            `${this.turnspeed} Deg/Sec`,
            this.aadamage.toString(),
            this.aaw==1?"yes":""
        ];
        return attr.concat(base_attr);
    }
    public exportAsLauncher(){
        let base_attr = super.export();
        let attr = [
            this.name,
            'x'+this.klass.count,
            "x"+this.barrelnum,
            this.hedamage,
            this.range,
            time2sec(this.reloadtime),
            '',
        ];
        return attr.concat(base_attr);
    }
}